<template>
	<div id="predict">
		predict page

		<icon name="refresh" spin></icon>
		
		<img id="photo" v-if="photourl" :src="photourl"/>

		<ul v-if="results">
			<li v-for="item in results">
				<div>text: {{item.text}}</div>
				<div>image: {{item.image_url}}</div>
				<div v-if="item.audio_url">audio: {{item.audio_url}}</div>
				<div v-if="item.video_url">audio: {{item.video_url}}</div>
			</li>
		</ul>
		
		<a href="javascript:void" @click="takePhoto"><icon name="camera" scale=3></icon></a>
	</div>
</template>

<script>
export default {
	name:"Predict",
	data() {
		return {
			error:"",
			photourl:"",
			results:[]
		}
	},
	methods: {
		takePhoto: function() {
			this.photourl = "";
			navigator.camera.getPicture(this.onTakePhotoSuccess, this.onTakePhotoFail, {  
		       quality: 50,  
		       destinationType: Camera.DestinationType.DATA_URL,  
		       encodingType: Camera.EncodingType.JPEG,  
		       sourceType: Camera.PictureSourceType.CAMERA
		   });
		},

		onTakePhotoSuccess: function(imageData) {
			this.photourl = "data:image/jpeg;base64," + imageData;
			axios.post("http://100.0.245.19:8081/predict", qs.stringify({"image": this.photourl})).then(response => {
				console.log('aitourscene:'+response.data);
				if (response.data.error) {
					this.error = response.data.error;
				} else {
					this.results = response.data.results;
				}
			  }).catch(function(error) {
			  	alert(error);
			  });
		},

		onTakePhotoFail: function(message) {
			alert('Failed because: ' + message);  
		}
	}
}
</script>



