<template>
	<div class="login">
		<img src="../assets/logo.png">
		<h1>Login</h1>
		<div class="container">
			<div class="row">
				<div class="col-md-3 col-md-offset-2">
					<span>Name:</span>
				</div>
				<div class="col-md-5">
					<input v-model="name"/>
				</div>
			</div>

			<div class="row">
				<div class="col-md-3 col-md-offset-2">
					<span>Password:</span>
				</div>
				<div class="col-md-5">
					<input type="password"/>
				</div>
			</div>

			<div class="row" v-if="error">
				{{error}}
			</div>
			<div class="row">
				<button @click.prevent="login">Login</button>
				<button @click.prevent="cancel">Cancel</button>
			</div>
		</div>
	</div>
</template>


<script>
import router from '../router';

export default {
	name: 'Login',
	data() {
		return {
			error: '',
			name: '123',
			password: ''
		}
	},
	methods: {
		login: function() {
			if (this.name == "123") {
				localStorage.setItem("authToken", "temptoken");
				this.error = "";
				var redirect = this.$route.query.redirect;
				console.log("you are login, forward to:");
				console.log(this.$route);
				router.replace(redirect);
			} else {
				this.error = "invalid username or password";
			}
		},

		cancel: function() {
			router.replace('/');
		}
	}
}
</script>

<style scoped>
.login {
	background-image: url('../assets/travel-login.png');
}
</style>


